import React,{useContext} from 'react'
import {Link} from 'react-router-dom'
import { ProductContext, StateContext } from '../context'
import {
  Container,
  Dropdown,
  Menu,
  Icon,
  Button
} from 'semantic-ui-react'


const Navbar = () => {


   const [product, setProduct] = useContext(ProductContext)
   const [nav, setNav]  = useContext(StateContext) 
   
  const logOutHandler = (e) =>{
    e.preventDefault()
    setNav({
      category:[],
      islogin: false,
      count: 0,
    })

  }
  
  
   

    return(
       <div>
        <Menu  inverted color='black'>
          <Container>
            <Menu.Item  header>
              <Icon name='compass'/>
              Ecommerce
            </Menu.Item>
            <Menu.Item>
              <Link to='/' onClick={e=>setProduct({
                          category : null,
                          loading: product.loading, 
                          user: product.user,
                          error: product.error,
                        })}>
                Все товары
              </Link>
            </Menu.Item>
            {nav.islogin && 
            <Dropdown item simple text='Категории'>
              <Dropdown.Menu>

                {nav.category.map(cat=>{
                  
                  return(
                    <Dropdown.Item key={cat.id}>
                      <Menu.Item>
                        <Link id={cat.id} to={'/'} style={{'color': 'black'}} onClick={e=>setProduct({
                          category : parseInt(e.target.id),
                          loading: product.loading, 
                          user: product.user,
                          error: product.error,
                        
                        
                        })}>
                           {cat.name}
                        </Link>
                      </Menu.Item>
                    </Dropdown.Item>
                  )
                })}
              </Dropdown.Menu>
            </Dropdown>}
         

            
              
              {nav.islogin ? 
              <Menu.Menu position='right'>
                 <Menu.Item>
                  <Link to='/cart'>
                 <Icon name='shopping cart'>
                  <p style={{ position:'absolute', 
                              color: '#65dbff',
                              top: '20px',
                              left: '35px',
                              fontSize: '14px',
                              fontWeight: '800',}}> {nav.count}  </p></Icon>
                    </Link>
                </Menu.Item>
                <Menu.Item>
                  <Link to='/'>
                   <Button onClick={logOutHandler}>Выйти</Button>
                  </Link> 
                </Menu.Item>
              </Menu.Menu>  
              : <Menu.Menu position='right'>
                <Menu.Item>
                    <Link to='/signin'>
                      <Button primary>Войти</Button>
                    </Link>
                  </Menu.Item>
                  <Menu.Item>
                    <Link to='/signup'>
                    <Button >Зарегистрироваться</Button>
                    </Link> 
                  </Menu.Item>
              </Menu.Menu> 
            
            }
            
          </Container>
        </Menu>   
      </div>
    )
  
}
 

export default Navbar